.recipe-form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 10px;
    padding: 15px;
    border-radius: 10px;
    border: 7px solid green;
  }
  
  .recipe-form input[type=text] {
      background: white;
      padding: 5px 8px;
      box-sizing: border-box;
  }
  
  .recipe-form-line {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-top: 5px;
  }
  
  .recipe-form-line input {
    margin-left: 15px;
  }

  .recipe-form-line label {
    color: red;
    font-family: 'Margarine', cursive;

  }
  
  .buttons {
    color: beige;
    background-color: red;
    border-radius: 6px;
    padding: 0 10px 1px 10px;
    margin: 8px 0;
    align-self: flex-end;
    font-size: 1.1em;
  }
  
  .close-button {
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    font-size: 0.9em;
    color: red;
    text-shadow: 0 1px 0 #fff;
    align-self: flex-end;
  }
  
  .recipe-form-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  
  #recipe-instructions-input{
     width:90%;

  }
.recipe-card {
    width: 31%;
    min-width: 240px;
    margin: 1%;
    border: 1px solid rgba(160,160,160,0.2);
    background-color:beige;
    border-radius: 0 0 2px 2px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
  }
  
  
  .recipe-card-content {
    color:red;
    padding: 16px;
    font-family: 'Special Elite';
    font-style: normal;
  }
  
  
  .recipe-card-image {
    overflow: hidden;
    height: 50%;
  }
  
  .recipe-card img {
    width: 100%;
    max-height: 250px;
  }
  
  .recipe-title {
    margin: 0;
  }

  





#animated-shadow-quote {
  display: inline-block;
  margin: 1em;
  max-width: 5em;
  position: relative;
}
#animated-shadow-quote div {
  -webkit-animation: shadows 2s linear infinite alternate;
          animation: shadows 2s linear infinite alternate;
  display: inline-block;
  margin: 0;
  padding: 0.09em;
}
#animated-shadow-quote div cite {
  display: block;
  font-style: italic;
  text-align: right;
}
#animated-shadow-quote div cite:before {
  content: "- ";
}

@-webkit-keyframes shadows {
  0% {
    box-shadow: 0 2px 4px -2px rgba(0, 0, 0, 0.25);
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.25);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes shadows {
  0% {
    box-shadow: 0 2px 4px -2px rgba(0, 0, 0, 0.25);
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.25);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

  
