/*cart*/
.place-order {
    cursor: context-menu;
    width: 140px;
    height: 140px;
    background-image: url(https://media.giphy.com/media/xUOxf6bcO61kVJuV3y/giphy.gif);
    background-size: 160px 120px;
    background-repeat: no-repeat;
    outline: 0;
    color: #333;
    text-transform: uppercase;
    transition: all 0.1s ease-in-out;
    border: 0;
    border-radius: 80px;
}
  .place-order span {
    color: #ffcab6;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all 0.15s ease-in-out;
    font-size: 38px;
  }
  .place-order span:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  .place-order:hover {
    background: #ff6127;
    color: #111;
  }
  .place-order:hover span {
    -webkit-transform: scale(1);
            transform: scale(1);
    color: white;
  }
  .place-order:active {
    background: #b63100;
  }
  
  .modal {
    width: 60%;
    height: 300px;
    left: 0;
    top: 80%;
    opacity: 0;
    margin-left: 20%;
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    pointer-events: none;
    transition: all 0.35s ease-in-out;
  }
  @media screen and (max-width: 500px) {
    .modal {
      width: 90%;
      margin-left: 5%;
    }
  }
  .modal[data-status="true"] {
    opacity: 1;
    pointer-events: auto;
    top: 50%;
    margin-top: -150px;
  }
  .modal[data-status="true"] .modal-left {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  .modal[data-status="true"] .modal-right {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    opacity: 1;
  }
  .modal[data-status="true"] .close, .modal[data-status="true"] .price-tag {
    -webkit-transform: translate(0);
            transform: translate(0);
    opacity: 1;
  }
  
  .modal-left {
    -webkit-box-shadow: -41px -20px 37px -1px rgba(201,219,64,1);
-moz-box-shadow: -41px -20px 37px -1px rgba(201,219,64,1);
box-shadow: -41px -20px 37px -1px rgba(201,219,64,1);
    left: 0;
    width: 50%;
    Height: 100%;
    -webkit-transform: translate(-50px, -20px) rotate(30deg);
            transform: translate(-50px, -20px) rotate(30deg);
    -webkit-transform-origin: left top;
            transform-origin: left top;
    background-image: url("https://cdn.shopify.com/s/files/1/0153/2051/products/joan-cornell---untitled-15_30830538296_o_grande.jpg?v=1482540429");
    background-size: cover;
    transition: -webkit-transform 0.45s ease-in-out;
    transition: transform 0.45s ease-in-out;
    transition: transform 0.45s ease-in-out, -webkit-transform 0.45s ease-in-out;
  }
  
  .modal-right {
    -webkit-box-shadow: 14px 14px 16px -1px rgba(204,170,34,0.69);
-moz-box-shadow: 14px 14px 16px -1px rgba(204,170,34,0.69);
box-shadow: 14px 14px 16px -1px rgba(204,170,34,0.69);
    left: 0;
    width: 50%;
    Height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url("https://media.giphy.com/media/3o7btW9O7p4qLjQNt6/giphy.gif");
    background-position: bottom;
    position: relative;
    opacity: 0;
    -webkit-transform: rotateY(-80deg);
            transform: rotateY(-80deg);
    -webkit-transform-origin: left center;
            transform-origin: left center;
    transition: opacity 0.45s ease-in-out 0.45s, -webkit-transform 0.45s ease-in-out 0.45s;
    transition: transform 0.45s ease-in-out 0.45s, opacity 0.45s ease-in-out 0.45s;
    transition: transform 0.45s ease-in-out 0.45s, opacity 0.45s ease-in-out 0.45s, -webkit-transform 0.45s ease-in-out 0.45s;
  }
  .modal-right h2 {
    color: red;
    margin: 0;
    font-size: 28px;
    font-family: 'VT323', monospace;

  }
  .modal-right p {
    color: red;
    text-align: center;
    
    padding: 0 20px;
    font-size: 21px;
    font-family: 'VT323', monospace;
  }
  .modal-right .close {
    cursor: pointer;
    position: absolute;
    bottom: 10px;
    right: 10px;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    outline: none;
    opacity: 0;
    -webkit-transform: translate(50px, 50px);
            transform: translate(50px, 50px);
    transition: all 0.45s ease-in-out 0.85s;
  }
  .modal-right .close span {
    color: black;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    transition: all 0.15s ease-in-out;
    font-size: 35px;
  }
  .modal-right .close:hover span {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  .modal-right .close:active {
    background: black;
  }
  
  @media (min-width: 490px) and (max-width: 690px) {
    .modal-right p {
      color: red;
      text-align: center;
      
      padding: 0 20px;
      font-size: 18px;
      font-family: 'VT323', monospace;
    }
    }


  @media (min-width: 20px) and (max-width: 490px) {
    .modal-right p {
      color: red;
      text-align: center;
      
      padding: 0 20px;
      font-size: 15px;
      font-family: 'VT323', monospace;
    }
    }